
<script>

    import { editOptions } from '../stores/EditOptions.js';
    import { currentItem } from '../stores/CurrentItem.js';

    const handleClick = (e) => {
        $editOptions.currentTab = e.target.id;
        $currentItem.currentTab = e.target.id;
    }

</script>
 

 
<div class="tabs">

    {#each $editOptions.tabs as item}
        <div 
            id={item} 
            class={item === $editOptions.currentTab ? "tab-active" : "tab"}
            on:click={handleClick}>
            {item}
        </div>    
    {/each}

</div>



<style>

    .tabs{
        display:flex;
        gap:16px;
        width: 100%;
        color:rgb(160, 160, 160);
    }
    .tab {
        font-size: 14px;
        padding: 1px 16px 6px 16px;
        border-bottom: 2px solid rgba(60, 60, 60, 0);
    }
    .tab:hover {
        color:white;
        border-bottom: 2px solid rgb(26, 210, 121);
    }
    .tab-active {
        font-size: 14px;
        color:white;
        padding: 1px 16px 4px 16px;
        border-bottom: 2px solid rgb(22, 164, 96);
    }

</style>