<script>
     import { onMount } from 'svelte';
    import { composerItems} from '../../stores/ComposerItems.js';
    import { dragInfo } from '../../stores/DragInfo.js';
    import Item from  './Item.svelte';
    import { currentItem } from '../../stores/CurrentItem.js';
    
    let height = 32;
    export let rowId = "";
    export let columnId = "";
    export let columnProps = {};
    export let hilite = false;
   
    let itemIsHilited = false;
    $: itemHilite = itemIsHilited;
    let showInsertBottom = "insertHintBelow insertHidden";
    $: insertBottom = showInsertBottom;
    

    const handleMouseEnter = (e) => {
       
        e.stopPropagation();
        hilite = !$dragInfo.isOverItem;
        $dragInfo.mouseenter.column = columnId;
        $dragInfo.mouseenter.row = rowId;
    }


    const handleMouseMove = (e) => {

        // e.stopPropagation();

        hilite = !$dragInfo.isOverItem;

        if (e.offsetY+5 < height/2) {
            showInsertBottom = "insertHintBelow insertHidden";
        } else {
            showInsertBottom = "insertHintBelow insertColor";
        }
    }

    // onMount(() => {
    // });


    const handleMouseDown = (e) => {

        e.stopPropagation();
        
        $dragInfo.mousedown = { 
            container: "rowsInUse",
            rowId: rowId,
            columnId: columnId,
            item: false,
        }
        $dragInfo.active.item = false;
        $dragInfo.currentType = "column";
    }


    const handleMouseLeave = (e) => {

        e.stopPropagation();
        // e.target.removeEventListener("mousemove", handleMouseMove);
        hilite = false;
        showInsertBottom = "insertHintBelow insertHidden";
        $dragInfo.mouseenter.column = false;
    }



</script>


<div 
    class={ 
        columnProps.classes + ((hilite && !itemHilite)  
        ? " column hilight" 
        : " column")}
    on:mouseenter={handleMouseEnter}
    on:mousedown = {handleMouseDown}
    on:mousemove={handleMouseMove}
    on:mouseleave={handleMouseLeave}
    >
    {#each columnProps.items as item}
        <Item 
        rowId = {rowId}
        columnId = {columnId}   
        id={item}
        >
        </Item>
    {/each}

    <!-- <div class={insertBottom}></div> -->
</div>

 <style>

    .column {
        position: relative;
        background-color:rgb(90, 90, 90);
        min-height: 24px;
        padding: 4px 3px 3px 3px;
        border: 1px solid rgb(90, 90, 90);
        user-select: none;
        cursor: default;
    }

    .hilight {
        border: 1px solid rgb(112, 112, 112);
    }
    .insertHintBelow {
        width: 100%;
        height: 1px;
        margin-bottom:0px;
        transform: translateY(-4px);
    }
    .insertColor {
        background-color: rgb(255, 191, 0);
    }
    .insertHidden {
        background-color: rgba(0, 0, 0, 0);
    }

    

 </style>