import { writable } from 'svelte/store';

export const currentItem = writable(
    {
        itemId:"",
        columnId:"",
        rowId:"",
        currentTab:"Item",
        item: false,
        column:false,
        row:false,
     }
);

export const currentItemReset = writable(
    {
        itemId:"",
        columnId:"",
        rowId:"",
        currentTab:"Item",
        item: false,
        column:false,
        row:false,
        }
);

export const currentItemExists = (obj) => {
    
    if (obj.itemId === "" && obj.columnId === "" && obj.rowId ==="") {
        return false;
    }
    if (obj.itemId !== "") {
        return obj.itemId;
    }
    if (obj.columnId !== "") {
        return obj.columnId;
    }
    if (obj.rowId !== "") {
        return obj.rowId;
    }
};