<script>
    import { onMount } from 'svelte';
    import { layout } from '../../../stores/Layout.js';
    import { currentItem } from '../../../stores/CurrentItem.js';
    import { contentItems } from '../../../stores/Content.js';
    import { forceRender } from '../../../stores/ForceRender.js';
    import Quill from 'quill';
   
    let quillEditor;
    let ready = false;
    let currItemId = "";
    let startTime;
    let endTime;
    $: if($currentItem.itemId != currItemId) { setContent() };


    const setContent = () => {

        if(!ready) return;

        let c = $contentItems[$currentItem.itemId].content;
        if ( c ) {
            quillEditor.setContents("");
            quillEditor.clipboard.dangerouslyPasteHTML(0, c); 
        }
        else {
            quillEditor.setContents("");
            quillEditor.clipboard.dangerouslyPasteHTML(0, ""); 
        }
    }


    const triggerRender = () => {

        let html = quillEditor.root.innerHTML;
        let outer  = quillEditor.root.outerHTML;
       
        $contentItems[$currentItem.itemId].content = html;
        $forceRender = true;
    }

    
    const handleQuillContentChange = (e) => {
            console.log(e);
    }

  


    onMount(  () => {
        
        let container = document.getElementById('editor');
        quillEditor = new Quill(container, {
        modules: {
            toolbar: [
            [{ header: [1, 2, 3, 4, false] }],
            [{ color: ["rgb(224, 64, 242)",
            "rgb(249, 141, 10)",
            "rgb(22, 75, 206)",
            "rgb(41,44,51)",
            "rgb(237,237,242)",
            "rgb(74, 165, 8)",
            "rgb(154, 32, 165)",
            "rgb(78, 152, 230)",
            "rgb(223, 72, 7)",
            "rgb(78, 152, 230)",
            "rgb(249, 249, 250)",
            "rgb(233, 236, 239)",
            "rgb(222, 226, 230)",
            "rgb(206, 212, 218)",
            "rgb(171, 181, 183)",
            "rgb(147, 156, 159)",
            "rgb(108, 117, 120)",
            "rgb(73, 80, 87)",
            "rgb(52, 58, 64)",
            "rgb(33, 37, 41)"] }],
            ["bold", "italic", "underline"],
            [{ 'align': [] }],
            [{ 'indent': '-1'}, { 'indent': '+1' }], 
            [{ 'list': 'ordered'}, { 'list': 'bullet' }],
            ["link", "clean"]
        ]
        },
        placeholder: "Type something...",
        theme: "snow"
        });

        quillEditor.on('text-change', function(delta, oldDelta, source) {

                if (source == 'api') {
                } else if (source == 'user') {
                    
                //     endTime = performance.now();
                    
                //     if ((performance.now()-startTime)/1000 > 2.0) {
                //         triggerRender();
                //     }
                    
                //     startTime = performance.now();
                triggerRender();
                }
        });

        // quillEditor.clipboard.dangerouslyPasteHTML(0, $contentItems[$currentItem.itemId].content);
        startTime = performance.now();
        ready = true;
        setContent();
	});


</script>

