
<script>

    import { onMount } from 'svelte';
    import { layout } from '../../../stores/Layout.js';
    import { currentItem } from '../../../stores/CurrentItem.js';
    import { contentItems } from '../../../stores/Content.js';
    import BorderSlider from "../../../slider/BorderSlider.svelte";
    import { forceRender } from '../../../stores/ForceRender.js';
   
    let currentEditObj;
    $: padObj = $contentItems[$currentItem.itemId].props.padding;
    $: $currentItem, checkChange();


    const checkChange = () => {

        if ($currentItem.currentTab === "Item") currentEditObj = $currentItem.item;
        if ($currentItem.currentTab === "Column") currentEditObj = $currentItem.column;
        if ($currentItem.currentTab === "Row") currentEditObj = $currentItem.row;

        padObj =  currentEditObj.props.padding;
    }




    function callbackSlider(obj) {
       
        // let p =  $contentItems[$currentItem.itemId].props.padding;
        let p = currentEditObj.props.padding;
        if (obj.id === "a") {
           p.a  = obj.value;
           p.t  = obj.value;
           p.r  = obj.value;
           p.b  = obj.value;
           p.l  = obj.value;
           (obj.value === 0) ? p.str = "" : p.str = ["padding:", obj.value,"px;"].join("");

        } else {
           p[obj.id] = obj.value;
           p.str = ["padding:", p.t, "px ", p.r, "px ", p.b, "px ", p.l, "px; "].join("");
        }
        
        currentEditObj.props.padding = p;
        padObj = p;
        $forceRender = true;
    }

    onMount(() => {
        checkChange();
       
    });


</script>
    
    
    

<div class="background" style="height:{$layout.bottomHeight -40}px;">
    
    <div class="frame">  

        <div class="container">
            <BorderSlider id="a" value={padObj.a} callback={callbackSlider}/>
            <div style="height:40px;"></div>
            <BorderSlider id="t" value={padObj.t} callback={callbackSlider}/>
            <BorderSlider id="r" value={padObj.r} callback={callbackSlider}/>
            <BorderSlider id="b" value={padObj.b} callback={callbackSlider}/>
            <BorderSlider id="l" value={padObj.l} callback={callbackSlider}/>

        </div>
    </div>
</div>



<style>

    .background {
        width: 100%;
        min-height: 200px;
        padding:40px;
        background-color:rgb(180, 180, 183);
    }
    
    .frame {
        display:flex;
        gap: 20px;
    }
    .container{
        width: 320px;
        background-color: white;
        padding:40px 20px 20px 20px;
        -webkit-box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2); 
        box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2);
    }

 </style>


 