<script>
    
    import { layout } from '../../stores/Layout.js';
    import { renderTest } from "../../helper/RenderPreview.js"
    import { composerItems } from '../../stores/ComposerItems.js';
    import { orderRows } from '../../stores/OrderRows.js';
    import { contentItems } from '../../stores/Content.js';
    import { forceRender } from '../../stores/ForceRender.js';

    let frame;
    $: if($forceRender) { render() };

    const render = () => {
        let html = renderTest( $orderRows, $composerItems, $contentItems );
        frame.srcdoc = html;
        $forceRender = false;
    }

</script>


<div class="preview-container" style="height:{$layout.midContentHeight}px; width:{$layout.previewWidth}px;"> 
    
    <iframe 
        src='about:blank' 
        sandbox="allow-same-origin allow-scripts" 
        bind:this={frame} 
        style="width:{$layout.iframe[$layout.iframe.current]}px; min-height:5000px" 
        title="Preview" 
        class="iframe"
        scrolling="yes">
    </iframe>

</div>



<style>

    .preview-container {
        display:grid;
        grid-template-rows: 1fr;
        background-color:rgb(160, 160, 163); 
        width: 900px;
        overflow: scroll;
    }
    .iframe {
        border:none;
        margin: 0;
        padding:0;
        min-height:400px;
        overflow: scroll;
        
    }

</style>