import { writable } from 'svelte/store';

export const editOptions = writable(
    {
        tabs: ["Item", "Column", "Row"],
        tabsNoItem: ["Column", "Row"],
        currentTab: "Item",
        name: "Item name",
        itemOptions: ["Content", "Background", "Padding", "Shadow", "Border"],  
        columnOptions: ["Background", "Padding", "Shadow", "Border"],
        rowOptions: ["Background","Padding", "Shadow"],
        spaceOptions: ["Space", "Background"], 
        lineOptions: ["Line"], 
        currentOption: "Content",
        currentType: "html"
    }
  
);

export const switchOption = (obj) => {

    
    // console.log("switch, currentTab", obj.currentTab);

    if ( obj.currentTab === "Item") {

        if (obj.currentType === "html") {
            if ( obj.itemOptions.indexOf(obj.currentOption) === -1 ) {
                return "Content";
            };
        }
        if (obj.currentType === "space") {
            if ( obj.spaceOptions.indexOf(obj.currentOption) === -1 ) {
                return "Space";
            };
        }
        if (obj.currentType === "line") {
            if ( obj.lineOptions.indexOf(obj.currentOption) === -1 ) {
                return "Line";
            };
        }
    }

    if ( obj.currentTab === "Column") return "Background";
    if ( obj.currentTab === "Row") return "Background";
    return false;


}

