<script>

    import { layout } from '../../stores/Layout.js';
    import TextInput from "../../input/TextInput.svelte";
    import EditOptions from "./EditOptions.svelte";
    import { editOptions } from '../../stores/EditOptions.js';
    import Tabs from "../../tabs/Tabs.svelte";
    import { currentItem, currentItemExists } from '../../stores/CurrentItem.js';
    import { contentItems } from '../../stores/Content.js';


    const handleChangeName = (str) => {
        $contentItems[$currentItem.itemId].name = str;
    }

</script>


