        column:false,
        row:false,
     }
    ); -->


<div class="bottom-left" style="width:{$layout.leftWidth }px; height:360px;">
    
    {#if currentItemExists($currentItem)}

        <Tabs />
        <div style="height:40px;"></div>

    
            {#if $editOptions.currentTab === "Item"}
                {#if $editOptions.currentType === "html"}
                    <TextInput text={$contentItems[$currentItem.itemId].name} callback={handleChangeName}></TextInput>
                {/if}
                {#if $editOptions.currentType === "space"}
                    <div>{$contentItems[$currentItem.itemId].name}</div>
                {/if}

            {/if}


            {#if $editOptions.currentTab === "Column"}
                {$currentItem.columnId}
            {/if}
    

        <div style="height:20px;"></div>
        
        <EditOptions />

    {:else}
        <div class ="hint-nothing-there">Nothing selected</div>
    {/if}

</div>
 

