<script>

    // import { onMount } from 'svelte';
    import Radio from "./Radio.svelte";

    export let options = [];
    export let current = false; 
    export let callback;

    $:currentItem = current;

   
    const handleClick = (obj) => {
        current = obj.value;
       
        callback (obj);

    }

    // onMount(() => {
       
