<script>
     
     
    import { dragInfo, dragInfoReset } from '../../stores/DragInfo.js';
    import { dragDummy } from '../../stores/DragDummy.js';
    import { forceRender } from '../../stores/ForceRender.js';
    import { composerItems } from '../../stores/ComposerItems.js';
    import { currentItem, currentItemReset } from '../../stores/CurrentItem.js';
    import { contentItems } from '../../stores/Content.js';
    import { editOptions, switchOption } from '../../stores/EditOptions.js';
   
    export let id = "";
    export let columnId = "";
    export let rowId = "";
   
    
    let position = false;
    let rect;
    let showHilite = false;
    $: hilite = showHilite;
    let showInsertTop = "insertHintAbove insertHidden";
    $: insertTop = showInsertTop;
    let showInsertBottom = "insertHintBelow insertHidden";
    $: insertBottom = showInsertBottom;


    const setRefs = () => {
        
        $currentItem.itemId = id;
        $currentItem.columnId = columnId;
        $currentItem.rowId = rowId;
        $currentItem.row = $composerItems[rowId];
        $currentItem.column = $composerItems[rowId].columns[columnId];
        $currentItem.item = $contentItems[id];
    }
    

    const handleMouseEnter = (e) => {
   
        e.stopPropagation();
        e.target.addEventListener("mousemove", handleMouseMove);
        showHilite = true;
        rect = e.target.getBoundingClientRect(); 
        $dragInfo.mouseenter.item = id;
        $dragInfo.mouseenter.column = columnId;
        $dragInfo.mouseenter.row = rowId;
        $dragInfo.isOverItem = true;
       
    }


    const handleGlobalMouseMove = (e) => {
   
        if (!$dragInfo.dragging)  {
            if (Math.abs($dragInfo.mousedown.x - e.clientX) > 1 || Math.abs($dragInfo.mousedown.y - e.clientY) > 1) {
                $dragInfo.dragging = true;
                $dragInfo.status = "item drag";
                $dragInfo.type = "item";
                $dragDummy.html = e.target.outerHTML;
            }
        }
        $dragDummy.style = ["top:", e.clientY, "px; left:", e.clientX, "px;"].join("");
        $dragInfo.trashVisible = true;
    }


    const handleMouseMove = (e) => {
     
        // e.stopPropagation();
        position = false;
        let y = e.clientY - rect. top;
        
        if ($dragInfo.dragging &&  ( $dragInfo.status === "item drag" ||  $dragInfo.status === "item template dragging")) {
            if (e.clientY < rect.top + rect.height/2) {
                showInsertTop = "insertHintAbove insertColor";
                showInsertBottom = "insertHintBelow insertHidden";
                position ="above";
            } else {
                showInsertTop = "insertHintAbove insertHidden";
                showInsertBottom = "insertHintBelow insertColor";
                position ="below";
            }
        }
        $dragInfo.insertAt.position = position;
       
    }


    const handleMouseDown = (e) => {

        e.stopPropagation();
       
        $dragInfo.mousedown = { 
            container: "rowsInUse",
            row: rowId,
            column: columnId,
            item: id,
            x: e.clientX,
            y: e.clientY,
        }
        let currType = $contentItems[id].type;
        $dragInfo.currentType =  currType;
        $editOptions.currentType = currType;
       
        let newOpt = switchOption($editOptions);
        if (newOpt)  $editOptions.currentOption = newOpt;
        setRefs();
      
        $dragInfo.status = "item mousedown";
        $dragInfo.active.item = id;
        document.addEventListener("mousemove", handleGlobalMouseMove);
        document.addEventListener("mouseup", handleMouseUp);
    }
   

    const handleMouseLeave = (e) => {
       
        e.target.removeEventListener("mousemove", handleMouseMove);
        showHilite = false;
        showInsertTop = "insertHintAbove insertHidden";
        showInsertBottom = "insertHintBelow insertHidden";
        $dragInfo.isOverItem = false;
        $dragInfo.mouseenter.item = false; ////////////////<-----------
    }


    const handleMouseUp = (e) => {

        document.removeEventListener("mousemove", handleGlobalMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
        $dragInfo.status = false;
        $dragDummy.style = ["top:", -1000, "px; left:", 0, "px;"].join("");
        // if ($dragInfo.mouseenter.item === id ) return; 
        $dragInfo.trashVisible = false;
        moveItem ();
    }


    const moveItem = () => {
       
        let itemOrgId   = $dragInfo.mousedown.item;
        let rowOrgId    = $dragInfo.mousedown.row;
        let colOrgId    = $dragInfo.mousedown.column;
        if (!itemOrgId || !rowOrgId || !colOrgId) return;
        if (itemOrgId === $dragInfo.mouseenter.item )  return;
        
        // remove item from org column:
        let colOrg      = $composerItems[rowOrgId].columns[colOrgId] ;
        let orgIndex    = $composerItems[rowOrgId].columns[colOrgId].items.indexOf(itemOrgId);
        let rowTargetId = $dragInfo.mouseenter.row;
        let colTargetId = $dragInfo.mouseenter.column;
        if (!colTargetId && !$dragInfo.isOverTrash) return;
        let itemRemoved = colOrg.items.splice(orgIndex, 1);
        $composerItems[rowOrgId].columns[colOrgId].items = colOrg.items;
        
        // delete item
        if($dragInfo.isOverTrash) {
            $dragInfo.isOverTrash = false;
            $dragInfo = {...$dragInfoReset};
            $currentItem = {...$currentItemReset};
            $forceRender = true;
            return; 
        }
        
        let colTarget   = $composerItems[rowTargetId].columns[colTargetId];
        let targetItems = colTarget.items;

        // let insertIndex =  $composerItems[rowTargetId].columns[colTargetId].items.indexOf($dragInfo.mouseenter.item);
        let insertIndex = targetItems.indexOf($dragInfo.mouseenter.item);
        if (position === "below") insertIndex++;

       
        targetItems.splice(insertIndex, 0, itemRemoved[0]);
        
        $composerItems[rowTargetId].columns[colTargetId].items = targetItems;      
        $dragInfo = {...$dragInfoReset};
        $forceRender = true;
    }


    const findById = (arr, id) => {

        for ( let i = 0; i< arr.length; i++) {
            if (arr[i].id === id) return { index: i, value: {...arr[i]} };
        }
        return false;
    }

</script>



<div class="item-container"
    on:mouseenter={handleMouseEnter}
    on:mousedown={handleMouseDown}
    on:mouseleave={handleMouseLeave}
    >
    <div class={insertTop}></div>
    <div class="spacer"></div>
    <div class={ 
        ( $dragInfo.mousedown.item === id  ||  $dragInfo.active.item === id )
        ? "item isCurrent" 
        : (hilite ? "item hilite" : "item") }
    > 
    {$contentItems[id].name}
    </div>
    <div class="spacer2"></div>
    <div class={insertBottom}></div>
</div>



 <style>

    .item-container {
        position: relative;
        background-color: rgba(142, 69, 69, 0);
    }
    .item {
        position: relative;
        background-color:rgb(200, 200, 200);
        overflow:hidden;
        color:black;
        font-size: 13px;
        text-align: center;
        min-width: 100%;
        padding:0px 6px 0px 6px;   
        border-radius: 3px;
        border: 6px solid rgb(200, 200, 200);
    }
    .hilite {
        background-color:rgb(226, 226, 226);
        border: 6px solid rgb(226, 226, 226);
    }
    .isCurrent {
        background-color:rgb(131, 176, 255);
        border: 6px solid rgb(131, 176, 255);
    }
    
    .spacer {
        background-color: rgba(55, 255, 0,0);
        width: 100%;
        height: 4px;
    }
    .spacer2 {
        background-color: rgba(55, 255, 0,0);
        width: 100%;
        height: 3px;
    }
    .insertHintAbove {
        position:absolute;
        width: 100%;
        height: 1px;
        top: -1px;
    }
    .insertHintBelow {
        width: 100%;
        height: 1px;
    }
    .insertColor {
        background-color: rgb(55, 255, 0);
    }
    .insertHidden {
        background-color: rgba(0, 0, 0, 0);
    }
    
 </style>
