
<script>

    import { onMount } from 'svelte';
    import { layout } from '../../../stores/Layout.js';
    import { currentItem } from '../../../stores/CurrentItem.js';
    import { contentItems } from '../../../stores/Content.js';
    import RadioGroup from "../../../radio/RadioGroup.svelte";
    import { forceRender } from '../../../stores/ForceRender.js';
   
    let currentEditObj;
    let spaceClass;

    let radioOptions = [
        {label:"10", value:"f-space-10"},
        {label:"20", value:"f-space-20"},
        {label:"40", value:"f-space-40"},
        {label:"60", value:"f-space-60"},
        {label:"80", value:"f-space-80"},
        {label:"100", value:"f-space-100"},
    ]
    let responsiveOptions = [
        {label:"all sizes", value:10},
        {label:"all sizes, reduced on mobile", value:20},
        {label:"mobile only", value:40},
        {label:"60", value:60},
        {label:"80", value:80},
        {label:"100", value:100}
    ]
    $: $currentItem, checkChange();


    const checkChange = () => {

        if ($currentItem.currentTab === "Item") currentEditObj = $currentItem.item;
        if ($currentItem.currentTab === "Column") currentEditObj = $currentItem.column;
        if ($currentItem.currentTab === "Row") currentEditObj = $currentItem.row;
        
        spaceClass =  currentEditObj.classes;
    }

   const callbackRadio = (obj) => {
       
        let spaceItem = $contentItems[$currentItem.itemId]
        spaceItem.classes = obj.value;
        spaceItem.name = "Space "+obj.label;

        $forceRender = true;
    }

    onMount(() => {
        checkChange();
       
    });


</script>
    
    
    

<div class="background" style="height:{$layout.bottomHeight -40}px;">
    
    <div class="frame-wrapper">
    
        <div class="frame">  
            <div class="headline">Vertical space (px):</div>
            <div class="container">
                <RadioGroup options = {radioOptions} current= {spaceClass} callback = {callbackRadio}/>
            </div>
        </div>

        <!-- <div class="frame">  
            <div class="headline">Responsive:</div>
            <div class="container" style="width:400px;">
                <RadioGroup options = {responsiveOptions}/>
            </div>
        </div> -->

    </div>
</div>



