<script>

     import { layout } from '../../stores/Layout.js';
     import Icon24 from '../../icons/Icon24.svelte';

     const clickIcon = (str) => {
       
        if (str === "desktop") $layout.iframe.current = "big";
        if (str === "tablet")  $layout.iframe.current = "mid";
        if (str === "phone")   $layout.iframe.current = "small";
     }


</script>


<div class="right-side-head" style="height:{$layout.subHeadHeight}px">
    <div >
        <Icon24 type="desktop" isActive={$layout.iframe.current === "big"} callback = {clickIcon}></Icon24>
        <Icon24 type="tablet" isActive={$layout.iframe.current === "mid"}  callback = {clickIcon}></Icon24>
        <Icon24 type="phone" isActive={$layout.iframe.current === "small"} callback = {clickIcon}></Icon24>
    </div>
    <div class="headline">
        
    </div>
</div>


<style>

    .right-side-head {
        display:grid;
        grid-template-columns: 1fr 2fr;
        background-color:rgb(24,24,27); 
        width: 100%;
        min-height: 24px;
    }
    .headline {
        color:rgb(128, 128, 128);
        font-size: 14px;
        padding: 8px 0px 0px 0px;
    }
    
</style>