
<script>

    import { onMount } from 'svelte';
    import { layout } from '../../../stores/Layout.js';
    import { currentItem } from '../../../stores/CurrentItem.js';
    import { contentItems } from '../../../stores/Content.js';
    import BorderSlider from "../../../slider/BorderSlider.svelte";
    import ColorPicker from "../../../colorPicker/ColorPicker.svelte";
    import { forceRender } from '../../../stores/ForceRender.js';
   
    let picker;
    let currentEditObj;
    $: $currentItem, checkChange();
    let props = {width: 200, min:0, max: 30};
    $: borderObj = $contentItems[$currentItem.itemId].props.border;
    $: radiusObj = $contentItems[$currentItem.itemId].props.radius;


    $: checkChange = () => {

        if ($currentItem.currentTab === "Item") currentEditObj = $currentItem.item;
        if ($currentItem.currentTab === "Column") currentEditObj = $currentItem.column;
        if ($currentItem.currentTab === "Row") currentEditObj = $currentItem.row;
        borderObj = currentEditObj.props.border;
        radiusObj = currentEditObj.props.radius;
    }


    const callbackPicker = (rgbStr) => {
        currentEditObj.props.borderColor = rgbStr;
        createBorderString();
        $forceRender = true;
    }


    onMount(() => {
        picker.setColor(currentEditObj.props.borderColor);
    });


    function callbackBorderSlider(obj) {
        
        let b =  currentEditObj.props.border;
        let c =  currentEditObj.props.borderColor || "rgb(0,0,0)";

        if (obj.id === "a") {
           b.a  = obj.value;
           b.t  = obj.value;
           b.r  = obj.value;
           b.b  = obj.value;
           b.l  = obj.value;
           (obj.value === 0) ? b.str = "" : createBorderString();
        } else {
           b[obj.id] = obj.value;
           createBorderString();
        }
        currentEditObj.props.border = b;
        borderObj = b;
        $forceRender = true;
    }

    const createBorderString = () => {

        let b =  currentEditObj.props.border;
        let c =  currentEditObj.props.borderColor || "rgb(0,0,0)";
        let str = [
                "border-width:", 
                b.t, "px ", 
                b.r, "px ", 
                b.b, "px ", 
                b.l, "px; ",
                "border-style: solid; ",
                "border-color: ",
                c,
                "; "].join("");

        currentEditObj.props.border.str = str;
    }


    function callbackRadiusSlider(obj) {

        let r =  currentEditObj.props.radius;

        if (obj.id === "rall") {
           r.rall  = obj.value;
           r.rtl   = obj.value;
           r.rtr   = obj.value;
           r.rbr   = obj.value;
           r.rbl   = obj.value;
        //    (obj.value === 0) ? r.str = "" : "border-radius: " + obj.value + "px; ";
        } else {
           r[obj.id] = obj.value;
        }
        r.str = ["border-radius: ", r.rtl, "px ",  r.rtr, "px ",  r.rbr, "px ",  r.rbl, "px; "].join("");
       
        currentEditObj.props.radius = r;
        radiusObj = r;
        $forceRender = true;
    }

</script>
    
    
    

<div class="background" style="height:{$layout.bottomHeight -40}px;">
    
    <div class="frame">  

        <div class="container" style="width:{props.width}px;">
            <BorderSlider id="a" props={props} value={borderObj.a} callback={callbackBorderSlider}/>
            <div style="height:40px;"></div>
            <BorderSlider id="t" props={props} value={borderObj.t} callback={callbackBorderSlider}/>
            <BorderSlider id="r" props={props} value={borderObj.r} callback={callbackBorderSlider}/>
            <BorderSlider id="b" props={props} value={borderObj.b} callback={callbackBorderSlider}/>
            <BorderSlider id="l" props={props} value={borderObj.l} callback={callbackBorderSlider}/>
        </div>

        <div class="container" style="width:{props.width}px;">
            <BorderSlider id="rall" props={props} value={radiusObj.rall} callback={callbackRadiusSlider}/>
            <div style="height:40px;"></div>
            <BorderSlider id="rtl" props={props} value={radiusObj.rtl} callback={callbackRadiusSlider}/>
            <BorderSlider id="rtr" props={props} value={radiusObj.rtr} callback={callbackRadiusSlider}/>
            <BorderSlider id="rbr" props={props} value={radiusObj.rbr} callback={callbackRadiusSlider}/>
            <BorderSlider id="rbl" props={props} value={radiusObj.rbl} callback={callbackRadiusSlider}/>
        </div>

        <ColorPicker bind:this={picker} callback={callbackPicker} startColor={"#FBFBFB"}/>

    </div>
</div>



<style>

    .background {
        width: 100%;
        min-height: 200px;
        padding:40px;
        background-color:rgb(180, 180, 183);
    }
    .frame {
        display:flex;
        gap: 20px;
    }
    .container{
        width: 320px;
        background-color: white;
        padding:40px 20px 20px 20px;
        -webkit-box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2); 
        box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2);
    }

 </style>


 