<script>
    import { dragInfo, dragInfoReset } from '../../stores/DragInfo.js';
    import { dragDummy } from '../../stores/DragDummy.js';
    import { composerItems} from '../../stores/ComposerItems.js';
    import { orderRows } from '../../stores/OrderRows.js';
    import { forceRender } from '../../stores/ForceRender.js';
    import Column from "./Column.svelte";

    export let rowId ="";
   
    let rect;
    let showHilite = false;
    $: hilite = showHilite;
    let itemIsHilited = false;
    $: itemHilite = itemIsHilited;
    let showInsertTop = "insertHintAbove insertHidden";
    $: insertTop = showInsertTop;
    let showInsertBottom = "insertHintBelow insertHidden";
    $: insertBottom = showInsertBottom;
   
    

    const handleMouseEnter = (e) => {

        // e.stopPropagation();

        e.target.addEventListener("mousemove", handleMouseMove);
        showHilite = true;
        rect = e.target.getBoundingClientRect(); 

        // $dragInfo.mouseenter.container = "rowsInUse";
        $dragInfo.mouseenter.row = rowId;
    
    }


    const handleGlobalMouseMove = (e) => {

        if (!$dragInfo.dragging)  {
            if (Math.abs($dragInfo.mousedown.x - e.clientX) > 1 || Math.abs($dragInfo.mousedown.y - e.clientY) > 1) {
                $dragInfo.dragging = true;
                $dragInfo.status = "row drag";
                $dragInfo.type = "row";
                $dragDummy.html = e.target.outerHTML;
            }
        }
        $dragDummy.style = ["top:", e.clientY, "px; left:", e.clientX, "px;"].join("");
        $dragInfo.trashVisible = true;
    }


    const handleMouseMove = (e) => {

        let position = false;
        let y = e.clientY - rect. top;

        if ($dragInfo.dragging  &&  ( $dragInfo.status === "row drag" ||  $dragInfo.status === "row template dragging") ) {
            if (e.clientY < rect.top + rect.height/2) {
                showInsertTop = "insertHintAbove insertColor";
                showInsertBottom = "insertHintBelow insertHidden";
                position ="above";
            } else {
                showInsertTop = "insertHintAbove insertHidden";
                showInsertBottom = "insertHintBelow insertColor";
                position ="below";
            }
        }
       
        $dragInfo.insertAt.position = position;
    }


    const handleMouseDown = (e) => {

        e.stopPropagation();
       
        $dragInfo.mousedown.row = rowId;
        $dragInfo.mousedown.x = e.clientX;
        $dragInfo.mousedown.y = e.clientY;
        $dragInfo.status = "row mousedown";
        document.addEventListener("mousemove", handleGlobalMouseMove);
        document.addEventListener("mouseup", handleMouseUp);
        $dragDummy.html = e.target.outerHTML;
        $dragInfo.currentType = "row";
    }


    const handleMouseLeave = (e) => {
       
        e.target.removeEventListener("mousemove", handleMouseMove);
        showHilite = false;
        showInsertTop = "insertHintAbove insertHidden";
        showInsertBottom = "insertHintBelow insertHidden";
    }


    const handleMouseUp = (e) => {

        document.removeEventListener("mousemove", handleGlobalMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
        $dragInfo.status = false;
        $dragDummy.style = ["top:", -1000, "px; left:", 0, "px;"].join("");
        moveRow ($dragInfo.mousedown.row, $dragInfo.mouseenter.row, $dragInfo.insertAt.position);

    }


    const moveRow = (row, targetRow, position) => {
       
        if (!targetRow && !$dragInfo.isOverTrash) return;
        if(row === targetRow && !$dragInfo.isOverTrash) return;
        if(!row) return;
        let from =  $orderRows.indexOf(row);
        let to =  $orderRows.indexOf(targetRow);
        let arr = [...$orderRows];
        let element = arr[from];
       
        arr.splice(from, 1);
        
        // delele row:
        if($dragInfo.isOverTrash) {
            $orderRows = arr;
            $dragInfo.isOverTrash = false;
            $dragInfo = {...$dragInfoReset};
            $forceRender = true;
            return; 
        }

        arr.splice(to, 0, element);

        $orderRows = arr;
        $dragInfo = {...$dragInfoReset};
        $forceRender = true;
    }

</script>




<div class="row-container"
    on:mousedown={handleMouseDown}
    on:mouseenter={handleMouseEnter}
    on:mouseleave={handleMouseLeave}> 

    <div class={insertTop}></div>
    <div class="spacer"></div>
   
    <div    
        id={rowId} 
        class= { hilite ? "f-grid-main12 f-gap10 row hilite" : "f-grid-main12 f-gap10 row"}
        >
        {#each  Object.entries($composerItems[rowId].columns) as [colId, colProps] }
            <Column
                rowId = {rowId} 
                columnId = {colId}
                columnProps = {colProps}
            />
        {/each}
    </div>
    
    <div class="spacer2"></div>
    <div class={insertBottom}></div>
</div>



 <style>

    .row-container {
        position: relative;
    }

    .row {
        position: relative;
        
        background-color:rgba(80, 80, 80, 0);
        width: 100%;
        padding: 1px 0px;
        user-select: none;
        cursor: default;
        border-top: 1px solid rgba(117, 45, 45, 0);
        border-bottom: 2px solid rgba(48, 48, 49,0);
        border-left: 1px solid rgba(48, 48, 49,0);
    }

    .hilite {
        border-left: 1px solid rgba(48, 48, 49,0);
    }

    .cover {
        grid-template-columns: 1fr; 
        min-height: 28px;
        background-color:rgb(47, 47, 47); 
    }
    .spacer {
        background-color: rgba(55, 255, 0,0);
        width: 100%;
        height: 2px;
    }
    .spacer2 {
        background-color: rgba(55, 255, 0,0);
        width: 100%;
        height: 1px;
    }
    .insertHintAbove {
        position:absolute;
        width: 100%;
        height: 1px;
        top: 1px;
    }
    .insertHintBelow {
        width: 100%;
        height: 1px;
    }
    .insertColor {
        background-color: rgb(230, 22, 237);
    }
    .insertHidden {
        background-color: rgba(0, 0, 0, 0);
    }
   

 </style>

