<script>
    
    export let id ="slider id";
    export let value = 0;
    export let props = {width:300, min:0, max:100};
    export let callback = () => { console.log ("no callback found:") };


    const handleChangeValue = () => {
        callback ( {id:id, value: value} );
    }

</script>



<div class="slider-container">

    <div class={"border " + id} ></div>

    <div>
        <div style="height:0px;"></div>
        <input type="range" id={id} min={props.min} max={props.max} bind:value={value} class="slider-std" on:input = {handleChangeValue}>
    </div>

    <input type="number" min={props.min} max={props.max} bind:value={value} class="text-input">
    
</div>


