import { writable } from 'svelte/store';

export const layout = writable(
    {
        topHeight: 32,
        midHeight: "100%",
        fastEditHeight: 156,
        bottomHeight: 60,
        bottomHeightNormal: 60,
        bottomHeightExpanded: 400,
        leftWidth: 400,
        rightWidth:600,
        expandBottom: false,
        midContentHeight: 400,
        previewWidth: 600,
        bottomAreaHeight: 60,
        subHeadHeight: 32,
        displayComposerTemplates: "flex",
        displayComposerTemplatesSubHead: "grid",
        composerTemplatesHeight: 250,
        composerStructureHeight: 250,
        bgColor:"red",
        iframe: {
            big: 1080,
            mid: 800,
            small: 400,
            current: "big",
        }
    }
);



export const calculateLayout = ( inputObj, bottomExpanded ) => {
    
    let obj = {...inputObj};
    obj.expandBottom = bottomExpanded
    let gapsAndPadding = 20;

    if ( bottomExpanded ) {
        obj.midHeight = (window.innerHeight -obj.topHeight -obj.bottomHeightExpanded -gapsAndPadding);
        obj.bottomHeight = obj.bottomHeightExpanded;
        obj.composerStructureHeight = obj.midHeight - obj.subHeadHeight - 40;
        obj.displayComposerTemplates = "none";
        obj.displayComposerTemplatesSubHead = "none";
    }
    else {
        obj.midHeight = (window.innerHeight -obj.topHeight  -obj.bottomHeightNormal -gapsAndPadding);
        obj.bottomHeight = obj.bottomHeightNormal;
        obj.composerStructureHeight = obj.midHeight - obj.composerTemplatesHeight - obj.subHeadHeight*3 - obj.fastEditHeight;
        obj.displayComposerTemplates = "flex";
        obj.displayComposerTemplatesSubHead = "grid";
    }
    obj.midContentHeight = obj.midHeight - obj.subHeadHeight;
    obj.previewWidth = (window.innerWidth - obj.leftWidth - 50);
    return obj;
}
