<script>

    import { colors } from '../stores/Colors.js';
    export let callback = () => { console.log ("no color callback") };

    const handleClick = (e) => {

        if (e.target.id === "nocolor") {
            callback("rgba(0,0,0,0)");
        }
        else {
            callback(e.target.style.backgroundColor);
        }
    }

</script>


<div class="frame">

    <div class="bg-colors">

        {#each $colors.defaultColors as color, i}
        <div class="box">
            <div id={"color_"+i} class="bg-color" on:click={handleClick} style="background-color: {color}"></div>
        </div>
        {/each}

        {#each $colors.defaultGreys as grey, i}
        <div class="box">
            <div id={"grey_"+i} class="bg-color" on:click={handleClick} style="background-color: {grey}"></div>
        </div>
        {/each}

        <div class="box">
            <div id="white" class="bg-color" on:click={handleClick} style="background-color:rgb(255,255,255)"></div>
        </div>
        <div class="box">
            <div id="black" class="bg-color" on:click={handleClick} style="background-color:rgb(0,0,0)"></div>
        </div>
        <div class="box">
            <div id="nocolor" class="bg-color chess-background" on:click={handleClick}></div>
        </div>
        <div class="empty-box"></div>
        <div class="empty-box"></div>
       
    </div>

</div>
	


<style>

    .frame {
        width: 231px;
    }
    .bg-colors {
        display:flex;
        flex-wrap: wrap;
        width:231px;
    }
    .box {
        width: 23px;
        height: 23px;
    }
    .box:hover {
        border-bottom: 2px solid rgb(103, 185, 55);
    }
    .empty-box {
        width: 23px;
        height: 23px;
    }
    .bg-color {
        width: 18px;
        height: 18px;
        border: 1px solid rgb(60, 60, 60);
        border-radius: 100%;
        margin-left:1px;
    }
    .bg-color:hover {
        border: 1px solid rgb(103, 185, 55);
    }
    .chess-background {
        background-image: linear-gradient(45deg, #808080 25%, transparent 25%), linear-gradient(-45deg, #808080 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #808080 75%), linear-gradient(-45deg, transparent 75%, #808080 75%);
        background-size: 10px 10px;
        background-position: 0 0, 0 5px, 5px -5px, -5px 0px;
    }
   
 </style>