import { add_render_callback } from "svelte/internal";
import { composerItems } from "../stores/ComposerItems.js"



const getHeader = () => {

    return `<!DOCTYPE html>
    <html lang="en">
    <head>
        <meta charset='utf-8'>
        <meta name='viewport' content='width=device-width,initial-scale=1'>
        <title>Preview</title>
        <link rel='icon' type='image/png' href='/favicon.png'>
        <link rel='stylesheet' href='/customer.css'>
        
    </head>
    <body style="background-color:rgb(240,240,240); margin:0px">`;
}

const getMainContainer = () => {

    return `<div >
    `;

    // return `<div class="f-maxwidth">
    // `;
}


const getFooter = () => {

    return `</body>
    </html>`;
}


export const renderTest = (order, rows, contentItems) => {
   
    let collect = [ getHeader() ];
    collect.push( getMainContainer() );

    for (let i = 0; i< order.length; i++) {

        let row = rows[order[i]];
        let open = ['<div ', getClassAndStylesRow(row.classes, row.props), ">"].join("");
        collect.push(open);

        for (const [id, obj] of Object.entries(row.columns)) {
            collect.push( renderColumn(obj, contentItems))
        }
         
        let close = "</div>";
        collect.push(close);
    }
    collect.push( "</div>" );   
    collect.push( getFooter() );   
    return collect.join(" ");
}



export const renderColumn = (col, contentItems) => {

    let collect = [];
    let open = ['<div ', getClassAndStylesColumn(col.classes, col.props), ">"].join("");
    collect.push(open);
    
    for (let i = 0; i< col.items.length; i++) {
        collect.push( renderItem( col.items[i], contentItems))
    }
    collect.push ("</div>") ;
    return collect.join("");
}



export const renderItem = (item, contentItems) => {
    
    let type = contentItems[item].type;
    let result = "";
    if( type === "html" ) {
        result = renderHtmlItem (item, contentItems);
    }
    if( type === "space" ) {
        result = renderSpaceItem (item, contentItems);
    }
    if( type === "line" ) {
        result = renderLineItem (item, contentItems);
    }
    
    return result;
}


export const renderHtmlItem = (item, contentItems) => {
   
    let itemContent = "no content";
    let itemObject = contentItems[item];
    if (itemObject) itemContent = itemObject.content;
    return ['<div ', getClassAndStyles(contentItems[item].props), '>' ,itemContent, '</div>'].join("");
}


export const renderSpaceItem = (item, contentItems) => {
   
    let itemObject = contentItems[item];
    return [
        '<div class="', 
        itemObject.classes, 
        ' "' ,
        ( itemObject.props.backgroundColor !== "") ? ['style="background-color:', itemObject.props.backgroundColor, ';" '].join("") : "",
        '></div>'].join("");
}


export const renderLineItem = (item, contentItems) => {
    return contentItems[item].props.str;
}



export const getClassAndStyles = (props) => {
    
    let classStr = [
        'class="',
        props.marginBottom,
        ' ',
        props.shadow,
        '" '
    ].join("");
    let styleStr = [
        'style="',
        (props.backgroundColor !== "") ? 'background-color:'+props.backgroundColor+'; ': '',
        props.padding.str,
        props.border.str,
        props.radius.str,
        '" '
    ].join("");
    return [classStr, styleStr].join(" ");
}


export const getClassAndStylesColumn = (standardClass, props) => {
    
    let classStr = [
        'class="',
        standardClass, 
        props.shadow,
        '" '
    ].join(" ");
    let styleStr = [
        'style="',
        (props.backgroundColor !== "") ? 'background-color:'+props.backgroundColor+'; ': '',
        props.padding.str,
        props.border.str,
        props.radius.str,
        '" '
    ].join("");
    return [classStr, styleStr].join(" ");
}


export const getClassAndStylesRow = (standardClass, props) => {
    
    let classStr = [
        'class="',
        standardClass, 
        props.shadow,
        '" '
    ].join(" ");
    let styleStr = [
        'style="',
        (props.backgroundColor !== "") ? 'background-color:'+props.backgroundColor+'; ': '',
        props.padding.str,
        props.border.str,
        props.radius.str,
        '" '
    ].join("");
    return [classStr, styleStr].join(" ");
}







export const getStyles = (obj) => {
    
    let padding = obj.padding.str;
    let bgColor = obj.backgroundColor;
    return ['style="', 'background-color:', bgColor,"; ", padding, '"'].join("");
}

