export const renderPreview = (structure, contentItems) => {
//    console.log("composerItems are", composerItems);
    for (let i = 0; i< structure.length; i++) {

        let row = structure[i];
        let open = ['<div ', 'id="', row.id, '"', ' class="', row.classes, '"',">"].join("");
        let content = getColumns(row.columns, contentItems);
        let close = "</div>";
        // console.log( [open, content, close].join(""));
    }
    return "Das klapot doch ganz gut";
}


export const getColumns = ( cols, contentItems ) => {

    let collect = [];

    for (let i = 0; i< cols.length; i++) {

        let col = cols[i];
        let open = ['<div ', ">"].join("");
        let content = getItems(col.items, contentItems);
        let close = "</div>";
        collect.push( [open,content,close].join(""));
    }
    return collect.join("");
}


export const getItems = ( items, contentItems ) => {

    let collect = [];

    for (let i = 0; i< items.length; i++) {

        let item = items[i];
        let open = ['<div ', ">"].join("");
        let content = getContent(contentItems, item.id);
        let close = "</div>";
        collect.push( [open,content,close].join(""));
    }
    return collect.join("");
}


export const getContent = (contentItems, id) => {
    
    for (let i = 0; i< contentItems.length; i++) {
        if (id === contentItems[i].id) return contentItems[i].content;
    }
    return "";
}

