<script>

    export let type = "";
    export let isActive = false;
    export let callback = () => { console.log ("no callback found:") };

    const getViewBoxString = (w,h) => {
        return ['viewBox="0 0 ', w, ' ', h,'" ','width="', w, 'px"'].join("")
    }

    const getIconType = () => {
       
        if (type === "desktop") {
            iconNormal = desktopNormal;
            iconActive  = desktopActive;
        }
        if (type === "laptop") {
            iconNormal = laptopNormal;
            iconActive  = laptopActive;
        }
        if (type === "tablet") {
            iconNormal = tabletNormal;
            iconActive  = tabletActive;
        }
        if (type === "phone") {
            iconNormal = phoneNormal;
            iconActive = phoneActive;
        }
    }


    let open        = `<svg xmlns="http://www.w3.org/2000/svg"`; 
    let closeOpen   = `><path fill="`;
    let fill_1      = `rgb(122,122, 122)`;
    let fill_2      = `rgb(37,185, 84)`;
    let closefill   =`" `;
    let desktopPath = `d="M24,2.4H6c-1.1,0-2,0.9-2,2v13c0,1.1,0.9,2,2,2h18c1.1,0,2-0.9,2-2v-13C26,3.3,25.1,2.4,24,2.4zM24.8,15.3c0,0.7-0.5,1.2-1.2,1.2H6.4c-0.7,0-1.2-0.5-1.2-1.2V4.8c0-0.7,0.5-1.2,1.2-1.2h17.2c0.7,0,1.2,0.5,1.2,1.2V15.3zM19.3,22.4h-8.7c0.1-0.5,0.6-0.8,1.1-0.8h0.5v-1h5.5v1h0.5C18.8,21.5,19.2,21.9,19.3,22.4z"`;
    let laptopPath  = `d="M24,20.5v0.6c0,0.7-0.5,1.2-1.2,1.2H4.2c-0.7,0-1.2-0.5-1.2-1.2v-0.6H24z M4,18.1V5.9c0-0.7,0.5-1.2,1.2-1.2h16.7c0.7,0,1.2,0.5,1.2,1.2v12.2c0,0.7-0.5,1.2-1.2,1.2H5.1C4.5,19.3,4,18.8,4,18.1z M5.2,18.3h16.7V5.9H5.2V18.3z"`;
    let tabletPath  = `d="M14.9,4.9H5.1c-0.7,0-1.2,0.5-1.2,1.2v15.1c0,0.7,0.5,1.2,1.2,1.2h9.7c0.7,0,1.2-0.5,1.2-1.2V6.1C16,5.5,15.5,4.9,14.9,4.9zM14.9,19.5H5.2l0-13.4h9.7L14.9,19.5z"`;
    let phonePath   = `d="M12.5,8.3H4.5c-0.3,0-0.6,0.3-0.6,0.6v12.8c0,0.3,0.3,0.6,0.6,0.6h7.9c0.3,0,0.6-0.3,0.6-0.6V8.9C13,8.6,12.8,8.3,12.5,8.3z M11.9,21.3H5.1V9.5h6.8V21.3z"`;
    let close       = `/></svg>`;
   

    let desktopNormal = [open, getViewBoxString(30,24), closeOpen, fill_1, closefill, desktopPath, close].join("");
    let desktopActive = [open, getViewBoxString(30,24), closeOpen, fill_2, closefill, desktopPath, close].join("");

    let laptopNormal = [open, getViewBoxString(27,24), closeOpen, fill_1, closefill, laptopPath, close].join("");
    let laptopActive = [open, getViewBoxString(27,24), closeOpen, fill_2, closefill, laptopPath, close].join("");

    let tabletNormal = [open, getViewBoxString(20,24), closeOpen, fill_1, closefill, tabletPath, close].join("");
    let tabletActive = [open, getViewBoxString(20,24), closeOpen, fill_2, closefill, tabletPath, close].join("");

    let phoneNormal  = [open, getViewBoxString(17,24), closeOpen, fill_1, closefill, phonePath, close].join("");
    let phoneActive  = [open, getViewBoxString(17,24), closeOpen, fill_2, closefill, phonePath, close].join("");
   
   
    let iconActive;
    let iconNormal;
    getIconType();
    
    $: current = (isActive) ? iconActive : iconNormal;


    const handleClick = () => {
        callback( type );
    }

    const handleMouseEnter = () => {
        current = iconActive;
    }

    const handleMouseLeave = () => {
        if( !isActive) current = iconNormal;
    }

</script>


 <div 
    class={"icon "} 
    on:click={handleClick}
    on:mouseenter={handleMouseEnter}
    on:mouseleave={handleMouseLeave}>
    
    {@html current}
   
</div>


 <style>

    .icon {
        display:inline-block;
        user-select:none;
        cursor:pointer;
    }

 </style>

