
<script>
    
    import { editOptions } from '../../stores/EditOptions.js';
      
    const handleClick = (e) => {
        $editOptions.currentOption = e.target.id;
    }

</script>


<div>

    {#if $editOptions.currentTab === "Item"}

        {#if $editOptions.currentType === "html"}
            {#each $editOptions.itemOptions as option}
                <div id={option} class={option === $editOptions.currentOption ? "option-active" : "option"}
                on:click={handleClick}>{option}</div>    
            {/each}
        {/if}

        {#if $editOptions.currentType === "space"}
            {#each $editOptions.spaceOptions as option}
                <div id={option} class={option === $editOptions.currentOption ? "option-active" : "option"}
                on:click={handleClick}>{option}</div>    
            {/each}
        {/if}

        {#if $editOptions.currentType === "line"}
            {#each $editOptions.lineOptions as option}
                <div id={option} class={option === $editOptions.currentOption ? "option-active" : "option"}
                on:click={handleClick}>{option}</div>    
            {/each}
        {/if}

    {/if}

    {#if $editOptions.currentTab === "Column"}
    {#each $editOptions.columnOptions as option}
        <div id={option} class={option === $editOptions.currentOption ? "option-active" : "option"}
        on:click={handleClick}>{option}</div>    
    {/each}
    {/if}

    {#if $editOptions.currentTab === "Row"}
    {#each $editOptions.rowOptions as option}
        <div id={option} class={option === $editOptions.currentOption ? "option-active" : "option"}
        on:click={handleClick}>{option}</div>    
    {/each}
    {/if}


</div>

   
<style>

    .option {
        height:32px;
        width: 100%;
        background-color: rgb(48, 48, 49);
        color: rgb(170, 170, 170);
        text-align: center;
        padding: 8px 5px 6px 5px;
        margin-bottom: 6px;
        border-radius: 4px;
    }
    .option:hover {
        background-color:rgb(90, 90, 90);
        color: rgb(200, 200, 200);
    }
    .option:active {
        background-color:#1a582c;
        color:rgb(255, 255, 255);
    }
    .option-active {
        height:32px;
        width: 100%;
        background-color:#1a582c;
        color:rgb(255, 255, 255);
        text-align: center;
        padding: 8px 5px 6px 5px;
        margin-bottom: 6px;
        border-radius: 4px;
    }
   
</style>