
<script>

    import { layout, calculateLayout } from '../../../stores/Layout.js';
    import { currentItem } from '../../../stores/CurrentItem.js';
    import { contentItems } from '../../../stores/Content.js';
    import { forceRender } from '../../../stores/ForceRender.js';
   
    let shadowClasses = [ 
        "f-shadow-small",
        "f-shadow-light",
        "f-shadow-medium",
        "f-shadow-dark",
        "f-shadow-custom-1",
        "f-shadow-custom-2",
        "f-shadow-custom-3"
    ];

    // $: $currentItem.itemId, itemHasChanged();
    $: current = "none";

    let currentEditObj;
    let picker;
    $: $currentItem, checkChange();

    
    const checkChange = () => {


        if ($currentItem.currentTab === "Item") currentEditObj = $currentItem.item;
        if ($currentItem.currentTab === "Column") currentEditObj = $currentItem.column;
        if ($currentItem.currentTab === "Row") currentEditObj = $currentItem.row;

        let currentClass = currentEditObj.props.shadow;
        if (!currentClass || currentClass === "") currentClass = "none";
        current = currentClass;

    }

    const handleClick = (e) => {

        current = e.target.id;
        let shadowClass = e.target.id;
        if (shadowClass === "none") shadowClass = "";
        currentEditObj.props.shadow = shadowClass;
        $forceRender = true;
    }

    
</script>
    


<div class="background" style="height:{$layout.bottomHeight -40}px; width:{$layout.previewWidth}px;">
    
    <div class="frame">  

        <div class="container">

            <div class={ (current === "none") ? "box-wrapper-hilite" : "box-wrapper" }>
                <div id={"none"} class={"box " } on:click={handleClick}>no shadow</div> 
            </div>

            {#each shadowClasses as item}
                <div class={ (item === current) ? "box-wrapper-hilite" : "box-wrapper" }>
                    <div id={item} 
                    class = {"box " +item }
                    on:click={handleClick}>
                    </div>
                </div>
            {/each}

        </div>
    </div>
</div>



<style>

    .background {
        width: 100%;
        min-height: 200px;
        padding:40px;
        background-color:rgb(180, 180, 183);
    }
    .frame {
        display:flex;
        gap: 20px;
    }
    .container{
        display: flex;
        flex-wrap: nowrap;
        gap:40px;
        width:100%;
        overflow-x: auto;
        background-color: white;
        padding:40px 40px 50px 40px;
        -webkit-box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2); 
        box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2);
    }
    .box {
        width: 80px;
        height: 120px;
        border: 1px solid rgb(202, 202, 202);
        color:rgb(97, 97, 97);
        text-align: center;
        padding: 30px 10px;
    }
    .current {
        background-color: #a4a9a6;
    }
    .box:hover {
        border: 1px solid #1a582c;
    }
    .box-wrapper {
        height: 140px;
    }
    .box-wrapper-hilite {
        height: 140px;
        border-bottom: 2px solid #1a582c;
    }

 </style>

