
export function getColorType (colorStr) {

    let type = false;
    let str = stripWhiteSpaces(colorStr);
    if(str.substring(0, 5) === "rgba(") { type = "rgba";};
    if(str.substring(0, 5) === "RGBA(") { type = "rgba";};
    if(str.substring(0, 4) === "rgb(") { type = "rgb";};
    if(str.substring(0, 4) === "RGB(") { type = "rgb";};
    if(str.substring(0, 4) === "hsl(") { type = "hsl";};
    if(str.substring(0, 4) === "HSL(") { type = "hsl";};
    if(str.substring(0, 1) === "#") { type = "hex";};
    return type;
}


function stripWhiteSpaces (str) {
    return str.replace(/\s+/g, '');
}


export function stringToRGB (colorStr) {

    let str = stripWhiteSpaces(colorStr);
    let split = str.split("(");
    let inner = split[1].split(")");
    let values = inner[0].split(",");
    let rgbaObj = {};

    rgbaObj.r = stringToInteger(values[0],255);
    rgbaObj.g = stringToInteger(values[1],255);
    rgbaObj.b = stringToInteger(values[2],255);
    if (values[3]) {
        rgbaObj.a = stringToFloat(values[3])
    }
    else {
        rgbaObj.a = 1.0;
    }
    return rgbaObj;
}


export const hexToRGB = (hexString) => {
    
    hexString = stripWhiteSpaces(hexString);
    let shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
    let hex = hexString.replace(shorthandRegex, function(m, r, g, b) {
      return r + r + g + g + b + b;
    });
    let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16),
      a: 1
    } : false;
}


export const rgbToHSV = (rgbObj) => {
  
    let r = rgbObj.r/255;
    let g = rgbObj.g/255;
    let b = rgbObj.b/255;
    let a = rgbObj.a || 1.0;
    let max = Math.max(r, g, b), min = Math.min(r, g, b);
    let h, s, v = max;
    let d = max - min;
    s = max == 0 ? 0 : d / max;
  
    if (max == min) {
      h = 0; //grey
    } else {
      switch (max) {
        case r: h = (g - b) / d + (g < b ? 6 : 0); break;
        case g: h = (b - r) / d + 2; break;
        case b: h = (r - g) / d + 4; break;
      }
      h /= 6;
    }
    return { h:parseInt(h*360), s:parseInt(s*100), v:parseInt(v*100), a: a };
}


export const hsvToRGB = (hsvObj) => {

    let h = hsvObj.h/360;
    let s = hsvObj.s/100;
    let v = hsvObj.v/100;
    let a = hsvObj.a || 1.0;
    let r, g, b;
  
    let i = Math.floor(h * 6);
    let f = h * 6 - i;
    let p = v * (1 - s);
    let q = v * (1 - f * s);
    let t = v * (1 - (1 - f) * s);
  
    switch (i % 6) {
      case 0: r = v, g = t, b = p; break;
      case 1: r = q, g = v, b = p; break;
      case 2: r = p, g = v, b = t; break;
      case 3: r = p, g = q, b = v; break;
      case 4: r = t, g = p, b = v; break;
      case 5: r = v, g = p, b = q; break;
    }
    return {r: parseInt(r * 255), g: parseInt(g * 255), b: parseInt(b * 255), a:a };
  }



export function rgbToString (rgbObj) {
    
    if (!rgbObj.a || rgbObj.a >= 1){
        return ["rgb(", rgbObj.r, ",", rgbObj.g, "," , rgbObj.b,  ")"].join("");
    }
    return ["rgba(", rgbObj.r, ",", rgbObj.g, "," , rgbObj.b, ",", rgbObj.a, ")"].join("");
}


export function getPureColorString (hsvObj) {

    let pureRGB = hsvToRGB( {h:hsvObj.h, s:100, v:100});
    return rgbToString (pureRGB, 1, false);
} 


export function rgbToHEX(rgbObj) {

    let r = rgbObj.r.toString(16);
    let g = rgbObj.g.toString(16);
    let b = rgbObj.b.toString(16);
  
    if (r.length == 1)
      r = "0" + r;
    if (g.length == 1)
      g = "0" + g;
    if (b.length == 1)
      b = "0" + b;
    return ["#",r,g,b].join("");
}



