<script>

    import { layout, calculateLayout } from '../stores/Layout.js';
    import { currentItem } from '../stores/CurrentItem.js';

    // export let title = "No title";
    // export let value = false;
    // export let callback = () => { console.log ("Button clicked, no callback found:", title, value ) };

    let arrowDownSVG = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 20"><path fill="rgb(70,70, 70)" d="M200,0,100,16,0,0Z"/></svg>`;
    let arrowDownSVGActive = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 20"><path fill="#1a582c" d="M200,0,100,16,0,0Z"/></svg>`;
    let arrowUpSVG = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 20"><path fill="rgb(70, 70, 70)" d="M0,16,100,0,200,16Z"/></svg>`;
    let arrowUpSVGActive = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 20"><path fill="#1a582c" d="M0,16,100,0,200,16Z"/></svg>`;

    let direction = "down";
    $: currentDirection = direction;

    let showSVG = arrowDownSVG;
    $: currentSVG = showSVG;


    const handleClick = () => {

        if ($currentItem.itemId === "" && $currentItem.columnId === "" && $currentItem.rowId === "") {
            return;
        }

        if (currentDirection === "down") {
            direction = "up";
            showSVG = arrowUpSVG;
            $layout = calculateLayout($layout, true);
        }
        else {
            direction = "down";
            showSVG = arrowDownSVG;
            $layout = calculateLayout($layout, false);
        }
        // callback( title, value );
    }

    const handleMouseEnter = () => {
        if ( currentDirection === "down") showSVG = arrowDownSVGActive;
        if ( currentDirection === "up") showSVG = arrowUpSVGActive;
    }

    const handleMouseLeave = () => {
        if ( currentDirection === "down") showSVG = arrowDownSVG;
        if ( currentDirection === "up") showSVG = arrowUpSVG;
    }

</script>


 <div 
    class={"more-button "} 
    on:click={handleClick}
    on:mouseenter={handleMouseEnter}
    on:mouseleave={handleMouseLeave}>
    {@html currentSVG}
   
</div>


 <style>

    .more-button {
        display:inline-block;
        background-color: rgb(48, 48, 49);
        color:white;
        text-align: center;
        outline: none;
        min-width: 60px;
        max-height: 40px;
        width: 100%;
        padding: 6px 16px 4px 16px;
        user-select:none;
        cursor:default;
    }

 </style>