<script>
    
    export let id ="slider id";
    export let label = "Slider Label:"
    export let value = 0;
    export let min = 0;
    export let max = 0;
    export let callback = () => { console.log ("no callback found:") };


    const handleChangeValue = () => {
        callback ( {id:id, value: value} );
    }

</script>



<div class="slider-container">
<div class="slider-label">{label}</div>
    <div>
        <div style="height:0px;"></div>
        <input type="range" id={id} min={min} max={max} bind:value={value} class="slider-std" on:input = {handleChangeValue}>
    </div>

    <input type="number" min={min} max={max} bind:value={value} class="text-input">
    
</div>



