<script>
    // import { afterUpdate } from 'svelte';
    import { dragInfo, dragInfoReset } from '../../../stores/DragInfo.js';
    import { dragDummy } from '../../../stores/DragDummy.js';
    import { orderRows } from '../../../stores/OrderRows.js';
    import { composerItems } from '../../../stores/ComposerItems.js';
    import { allowedRows } from '../../../stores/AllowedRows.js';
    import { forceRender } from '../../../stores/ForceRender.js';
    import { generateId } from '../../../helper/randomId.js';
    import ColumnTemplate from "./ColumnTemplate.svelte";
   
    export let id ="row";
    export let rowType = "row template";
    export let columns = [];
   
    let rect;
    let showHilight = false;
    $: hilight = showHilight;
   


    const handleMouseDown = (e) => {

        e.stopPropagation();
        rect = e.target.getBoundingClientRect(); 
        $dragDummy.html = e.target.outerHTML;
        $dragDummy.style = ["top:", -1000, "px; left:",-1000, "px; height:", rect.height,"px;" ].join("");
        $dragInfo.mousedown.container = "rowTemplates";
        $dragInfo.mousedown.row = id;
        $dragInfo.mousedown.x = e.clientX;
        $dragInfo.mousedown.y = e.clientY;
        
        $dragInfo.status = "row template mousedown";
        document.addEventListener("mousemove", handleGlobalMouseMove);
        document.addEventListener("mouseup", handleMouseUp);
    }


    const handleGlobalMouseMove = (e) => {

        if (!$dragInfo.dragging)  {
            if (Math.abs($dragInfo.mousedown.x - e.clientX) > 1 || Math.abs($dragInfo.mousedown.y - e.clientY) > 1) {
                $dragInfo.dragging = true;
                $dragInfo.type = "row template";
                $dragInfo.status = "row template dragging";
                // $dragDummy.html = e.target.outerHTML;
            }
        }
        $dragDummy.style = ["top:", e.clientY, "px; left:", e.clientX, "px; height:", rect.height,"px;" ].join("");
    }


    const handleMouseUp = (e) => {

        document.removeEventListener("mousemove", handleGlobalMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
        $dragInfo.status = false;
        $dragDummy.style = ["top:", -1000, "px; left:", 0, "px;"].join("");
        insertRow (id, $dragInfo.mouseenter.row, $dragInfo.insertAt.position);
    }


    const handleMouseEnter = (e) => {
       if ( $dragInfo.status !== "row template dragging") showHilight = true;
    }


    const handleMouseLeave = (e) => {
        showHilight = false
    }


    const insertRow = (row, targetRow, position) => {
        
        let ok = true;
        if( row == targetRow) ok = false; 
        if( !row) ok = false;
        if( !targetRow) ok = false;
        if( !position) ok = false;
       
        if (ok) {
            let templateCopy = {...$allowedRows[row]};
            let randId = generateId ("row_");
            templateCopy.id = randId;

            let at =  $orderRows.indexOf(targetRow);
            if (position === "below") at++;
            let arr = [...$orderRows];
            arr.splice(at, 0, randId);
            $orderRows = arr;
            $composerItems[randId] = {...templateCopy};
        }

        $dragInfo = {...$dragInfoReset};
        $forceRender = true;

    }

</script>


<div 
    id={id} 
    data-rowtype={rowType} 
    class= "f-grid-main12 f-gap10"
    on:mousedown={handleMouseDown}
    on:mouseenter={handleMouseEnter}
    on:mouseleave={handleMouseLeave}>

    {#each  Object.entries(columns) as [colId, column] }
        <ColumnTemplate classes={column.classes} hilight={hilight}/>
    {/each}


</div>


