<script>
    
    import {onMount} from 'svelte';
    import DefaultColors from "./DefaultColors.svelte";
    import { stringToRGB, rgbToHSV, hsvToRGB, rgbToString, getPureColorString, rgbToHEX } from '../helper/convertColor.js';

    export let callback = () => { console.log ("no color callback") };
    let tracked;
   
    let newColorBox;
    let oldColorBox;
    let picker;
    $: newColorStr = "rgba(255,0,0,1)";
    $: oldColorStr = "rgba(255,0,0,1)";
    $: pureColorStr = "rgb(255,0,0)";
    $: rgbString = "rgb(255,0,0)";
    $: hexString = "#ff0000";
    $: noColor = false;

    let rgba = {r:255, g:0, b:0, a:1.0};
    let hsv = {h:0, s:100, v:100, a:1.0};
   
    let w = 231;
    let hh = 96;
    let hueSlider;
    let alphaSlider;
   


    export function setColor (colorStr, oldCol) {
        
        if (!colorStr || colorStr === "" ) {
            noColor = true;
            colorStr = "rgba(255,0,0,1)"  
        } else {
            noColor = false; 
              
        }
        rgba = stringToRGB (colorStr);
        hsv = rgbToHSV(rgba);
        pureColorStr = getPureColorString(hsv);
        newColorStr = colorStr;
        oldColorStr = ( oldCol ) ? oldCol : colorStr;
        hexString = rgbToHEX (rgba);
        rgbString = newColorStr;
        updateCsPicker();
        updateSlider ();
       
    }


    const colorChangeCallback = () => {
        callback (rgbString);
    }


    const callbackDefaultColor = (colorStr) => {
        setColor (colorStr, oldColorStr);
        colorChangeCallback();
    }


    const setHueBySlider = (e) => {
        hsv.h = e.target.value;
        colorChange();
    }


    const setAlphaBySlider = (e) => {
       rgba.a = e.target.value/100;
       hsv.a = e.target.value/100;
       colorChange();
    }


    onMount(() => {
        document.addEventListener("mouseup", mouseUp);
        document.addEventListener("mousemove", mouseMove);
        document.addEventListener("mousedown", killTouchEvents);
        setColor("rgb(8,135,214)");
    });


    const csDown = (e) => {
       
        tracked = e.currentTarget;
        let xPercentage = (((e.offsetX + 1) / w) * 100).toFixed(2);
        let yPercentage = (((e.offsetY + 1) / hh) * 100).toFixed(2);
        picker.style.top = yPercentage + "%";
        picker.style.left = xPercentage + "%";
        hsv.s = parseInt(xPercentage);
        hsv.v = parseInt(100 - yPercentage);
        
        colorChange();
    }


    const mouseMove = (e) => {

        if (tracked) {
            let mouseX = e.clientX;
            let mouseY = e.clientY;
            let trackedPos = tracked.getBoundingClientRect();
            let xPercentage = (mouseX - trackedPos.x) / w * 100;
            let yPercentage = (mouseY - trackedPos.y) / hh * 100;
            (xPercentage > 100) ? xPercentage = 100: (xPercentage < 0) ? xPercentage = 0 : null;
            (yPercentage > 100) ? yPercentage = 100: (yPercentage < 0) ? yPercentage = 0 : null;
            yPercentage = yPercentage.toFixed(2);
            xPercentage = xPercentage.toFixed(2);
            picker.style.top = yPercentage + "%";
            picker.style.left = xPercentage + "%";
            hsv.s = parseInt(xPercentage);
            hsv.v = parseInt(100 - yPercentage);

            colorChange();
        }
    }


    const mouseUp = (e) =>{
        tracked = null;
    }


    const colorChange = () => {
   
        rgba =  hsvToRGB(hsv);
        newColorStr = rgbToString( rgba );
        pureColorStr = getPureColorString(hsv);
        hexString = rgbToHEX (rgba);
        rgbString = newColorStr;
        colorChangeCallback();
    }


    function beginColor() {
        
        hsv = rgbToHSV(rgba);
        updateCsPicker();
    }


    function removeEventListenerFromElement(elementId, eventName, listenerCallback) {
    
        let element = document.querySelector(elementId);
        if (element) element.removeEventListener(eventName, listenerCallback);
    }


    function killMouseEvents() {
    
        removeEventListenerFromElement("#colorsquare-event", "mousedown", csDown);
        document.removeEventListener("mouseup",mouseUp);
        document.removeEventListener("mousemove",mouseMove);
        document.removeEventListener("mousedown",killTouchEvents);
    }


    function killTouchEvents() {
    
        document.removeEventListener("touchend",mouseUp);
        document.removeEventListener("mousedown",killTouchEvents);
    }


    const updateCsPicker = () => {

        let xPercentage = hsv.s;
        let yPercentage = (100 - hsv.v);
        picker.style.top = yPercentage + "%";
        picker.style.left = xPercentage + "%";
    }


    const updateSlider = () => {
        alphaSlider.value = parseInt(rgba.a * 100);
        hueSlider.value = parseInt(hsv.h);
    }


    const clickOldColor = () => {
        setColor(oldColorStr);
        colorChangeCallback();
    }


</script>


<div class = "component-wrapper">

    <!-- color picker -->
    <div class="main-container">

        <div class="colorsquare size" style="background-color:{pureColorStr};">
            <div class="saturation-gradient">
                <div class="value-gradient">
                    <div bind:this={picker} id="colorsquare-picker"></div>
                    <div id="colorsquare-event" on:mousedown={csDown}></div>
                </div>
            </div>
        </div>

        <div style="height:10px;"></div>

        <input 
            class="hue-slider"
            bind:this={hueSlider} 
            type="range" 
            min="0" 
            max="360" 
            style="width:231px;"
            on:input={setHueBySlider}>
        
        <div style="height:6px;"></div>

        <div style ="position:relative">
            <div class="alpha-slider-chess-background " style="width:231px;"></div>
            <input 
                class="alpha-slider"
                bind:this={alphaSlider} 
                type="range" 
                min="0" 
                max="100" 
                style="width:231px;"
                on:input={setAlphaBySlider}>
            </div>
        
        <div style="height:35px;"></div>

        <DefaultColors callback={callbackDefaultColor}/>
        
    </div>


    <!-- info section -->
    <div style="width:200px">
        
        <div class="new-and-old">
            <div class="color-info-box">
                <div class="color-picked-bg">
                    <div bind:this={newColorBox} class="color-picked" style="background:{newColorStr}"></div>
                </div>
            </div>
            <div  class="color-info-box">
                <div class="color-picked-bg">
                    <div bind:this={oldColorBox} class="color-picked-old" style="background:{oldColorStr}" on:click={clickOldColor}></div>
                </div>
            </div>
        </div>

        <div style="height:8px;"></div>
        <div class="label-text">Hex</div>
        <div class="info-text">{hexString.toLowerCase()}</div>
        <div style="height:4px;"></div>
        <div class="label-text">RGB</div>
        <div class="info-text">{rgbString}</div>

    </div>


</div>


