<script>
    import { onMount } from 'svelte';
    import { layout, calculateLayout } from '../stores/Layout.js';
    import { dragDummy } from '../stores/DragDummy.js';
    import Header from "./top/Header.svelte";
    import LeftSide from "./left/LeftSide.svelte";
    import RightSide from "./right/RightSide.svelte";
    import Bottom from "./bottom/Bottom.svelte";

    let dummy;
    
    $: if (dummy) {dummy.innerHTML = $dragDummy.html; dummy.style = $dragDummy.style}

    
    onMount( () => {
        $layout = calculateLayout($layout, false);
        dummy.innerHTML = $dragDummy.html;
        dummy.style = $dragDummy.style;
    });


    const resizeListener = () => {
        $layout = calculateLayout($layout, $layout.expandBottom);
    }


    window.addEventListener("resize", resizeListener);


</script>



<div class="main-grid">
    <Header />
    <div class= "mid-area" style="height:{$layout.midHeight}px">
        <LeftSide />
        <RightSide />
    </div>
    <Bottom />
    <div 
        bind:this={dummy}
        id="dummy"
        class="dummy" >
    </div>
        
</div>
   

 <style>

    .main-grid {
        position: relative;
        max-height: 100vh;
        min-height: 100vh;
        padding:16px;

    }
    .mid-area { 
        display: grid;
        grid-template-columns: 400px 1fr;
        gap: 16px;
        min-height: 100px;
        width:100%;
        background-color: rgb(24,24,27); 
    }
    .dummy {
        position:absolute;
        min-width: 200px;
        min-height: 5px;
        user-select: none;
        opacity: 0.6;
        z-index: 999;
        pointer-events: none;
        top: 0px;
    }


    
 </style>
