
<script>

    import { onMount } from 'svelte';
    import { layout } from '../../../stores/Layout.js';
    import { currentItem } from '../../../stores/CurrentItem.js';
    import { contentItems } from '../../../stores/Content.js';
    import SliderAndInput from "../../../slider/SliderAndInput.svelte";
    import ColorPicker from "../../../colorPicker/ColorPicker.svelte";
    import { forceRender } from '../../../stores/ForceRender.js';
   
    let picker;
    let currentEditObj;
    let lineObj;
   
    
    $: $currentItem, checkChange();
    
    let props = {width: 200, min:0, max: 30};
    // $: lineObj = $contentItems[$currentItem.itemId];
    


    $:checkChange = () => {

        if ($currentItem.currentTab === "Item") currentEditObj = $currentItem.item;
        lineObj = currentEditObj.props;
        if (picker) picker.setColor(lineObj.backgroundColor);
    }


    const callbackPicker = (rgbStr) => {
        currentEditObj.props.backgroundColor = rgbStr;
        createLineString();
        // $forceRender = true;
    }

    const callbackMarginTop = (obj) => {
        lineObj.marginTop = obj.value;
        currentEditObj.props.marginTop = obj.value;
        createLineString();
        // $forceRender = true;
    }
    const callbackHeight = (obj) => {
        lineObj.height = obj.value;
        currentEditObj.props.height = obj.value;
        createLineString();
    }

    const callbackMarginBottom = (obj) => {
        lineObj.marginBottom = obj.value;
        currentEditObj.props.marginBottom = obj.value;
        createLineString();
        
    }

    const createLineString = () => {

        let str = [
                '<hr style="height:', 
                lineObj.height,
                'px; border-width:0; background-color:', 
                lineObj.backgroundColor, 
                '; margin-top:',
                lineObj.marginTop, 
                'px; margin-bottom:',
                lineObj.marginBottom,
                'px;" >'].join("");

        currentEditObj.props.str = str;
        // currentEditObj.props.marginTop = lineObj.marginTop;
        let realItem = $contentItems[$currentItem.itemId];
        realItem.props = {...lineObj};
        $forceRender = true;
    }


    onMount(() => {
        checkChange();
        
    });

</script>
    
    
    

<div class="background" style="height:{$layout.bottomHeight -40}px;">
    
    <div class="frame">  

        <div class="container" style="width:240px;">
            <SliderAndInput id="marginTop" label= "Above:" min=0 max=30 value={lineObj.marginTop} callback={callbackMarginTop}/>
            <SliderAndInput id="height" label= "Height:"min=1 max=30 value={lineObj.height} callback={callbackHeight}/>
            <SliderAndInput id="marginBottom" label= "Below:" min=0 max=30 value={lineObj.marginBottom} callback={callbackMarginBottom}/>
            
        </div>

        <ColorPicker bind:this={picker} callback={callbackPicker} startColor={"rgb=(0,0,0)"}/>

    </div>
</div>



<style>

    .background {
        width: 100%;
        min-height: 200px;
        padding:40px;
        background-color:rgb(180, 180, 183);
    }
    .frame {
        display:flex;
        gap: 20px;
    }
    .container{
        width: 320px;
        background-color: white;
        padding:40px 20px 20px 20px;
        -webkit-box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2); 
        box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.2);
    }

 </style>


 